@import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap');

/* Home section CSS */
#home, #about, #skills, #portfolio, #contact {
  min-height: 100vh;
}
#home nav, .nav-header, #home .profession, .bottomTrigger, .skills, .skills div, .projects, .projects .shadow-sm div .overlay {
  display: flex;
  align-items: center !important;
  justify-content: center;
}

#home nav {
  width: 100%;
  height: 50vh;
  background-color: #1D78D7;
  position: relative;
  align-items: flex-end;
  padding: 60px 0;
  z-index: 10;
  transition: .3s;
}

#home nav::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  border: 12px solid;
  border-color: #1D78D7 transparent transparent transparent;
  transition: .3s;
}

.scroll-home .nav-header {
  width: 100%;
  justify-content: space-between;
}

#home nav div .logo {
  padding: 0;
  color: #ffffff;
  font-size: 60px;
  font-family: 'Oswald', sans-serif;
  line-height: 1px;
  display: block !important;
  transition: .3s;
}

nav .fa-bars {
  color: #ffffff;
  font-size: 2rem;
  display: none;
  cursor: pointer;
}

nav .menus {
  display: none;
  background-color: #1D78D7;
  position: absolute;
  top: 80px;
  left: 0;
  right: 0;
  text-align: center;
}

nav .menus a {
  color: #ffffff;
  font-size: 18px;
  letter-spacing: 2px;
  padding: 8px 12px;
  width: 90%;
  display: block;
  margin: auto;
}

nav .menus a:hover {
  color: #1D78D7;
  background-color: #ffffff;
  border-radius: 4px;
  transition: .4s ease;
}

#home .profession {
  height: 40vh;
}

/* Onscroll CSS Start */
.scroll-home nav {
  height: 80px !important;
  justify-content: flex-start !important;
  padding: 0 15px !important;
  position: fixed !important;
  left: 0;
  top: 0;
  right: 0;
  box-shadow: 2px 2px 12px rgba(0,0,0,.2);
}

.scroll-home nav::after {
  content: "";
  border: none !important;
}

.scroll-home nav .fa-bars {
  display: inline-block;
}

.scroll-home nav div .logo {
  font-size: 2rem !important;
}

/* End */

#home .profession p {
  font-size: 36px;
  font-family: 'Oswald', sans-serif;
  line-height: 1px;
}

.bottomTrigger {
  position: absolute;
  right: 20px;
  bottom: 15px;
  width: 40px;
  height: 40px;
  color: #1D78D7;
  font-size: 20px;
  border-radius: 5px;
  box-shadow: 2px 2px 12px rgba(0,0,0,.2);
}

/* About Section CSS */
#about header p, .portfolio-text p:nth-child(1), #contact header p {
  font-size: 30px;
  font-family: 'Oswald', sans-serif;
  line-height: 1px;
  color: #424242;
  font-weight: 300;
}

#about header span, .portfolio-text span, #contact header span {
  font-size: 2rem;
  font-weight: 300;
  color: #414141;
  line-height: 1;
}

#about div p, .portfolio-text .description {
  font-family: 'Source Code Pro', monospace;
  font-size: 18px;
  line-height: 1.15;
  text-align: justify;
}

/* Skills section CSS */
#skills p {
  font-family: 'Oswald', sans-serif;
  font-size: 30px;
  color: #424242;
  /*line-height: 1;*/
}

#skills .skills {
  flex-wrap: wrap;
}

.skills div {
  border-radius: 50%;
  color: #ffffff;
  font-family: 'Source Code Pro', monospace;
  font-size: 18px;
  text-align: center;
  margin: 5px 10px;
  transition: .3s ease;
  box-shadow: 2px 2px 12px rgba(0,0,0,.2);
}

.skills div:hover {
  box-shadow: 2px 8px 16px rgba(0,0,0,.3);
  transform: scale(1.2);
}

.skills div:nth-child(1), .skills div:nth-child(5) {
  background: linear-gradient(
  #e34c26, #264de4);
  width: 80px;
  height: 80px;
}

.skills div:nth-child(2) {
  background: linear-gradient(#c69, #563d7c);
  width: 130px;
  height: 130px;
}

.skills div:nth-child(3) {
  background-color: #1D78D7;
  width: 140px;
  height: 140px;
}

.skills div:nth-child(4) {
  background: linear-gradient(#f0db4f, #61dbfb);
  width: 140px;
  height: 140px;
}

.skills div:nth-child(5) {
  background: #000000 !important;
}

/* Portfolio section CSS */
.portfolio-iframes {
  margin-top: 50px;
}

iframe {
  border: none;
  height: 100%;
}

.smartphone {
  position: relative;
  width: 360px;
  height: 640px;
  margin: auto;
  border: 16px black solid;
  border-top-width: 60px;
  border-bottom-width: 60px;
  border-radius: 36px;
  overflow: hidden;
}

.smartphone:before {
  content: '';
  display: block;
  width: 60px;
  height: 5px;
  position: absolute;
  top: -30px;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #333;
  border-radius: 10px;
}

.smartphone:after {
  content: '';
  display: block;
  width: 35px;
  height: 35px;
  position: absolute;
  left: 50%;
  bottom: -65px;
  transform: translate(-50%, -50%);
  background: #333;
  border-radius: 50%;
}

.smartphone .content {
  width: 360px;
  height: 640px;
  background: white;
}

.laptop {
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: scale(.6) translate(-50%);
  transform: scale(.6) translate(-50%);
  left: 50%;
  position: relative;
  width: 1200px;
  height: 800px;
  border-radius: 6px;
  border-style: solid;
  border-color: black;
  border-width: 24px 24px 80px;
  background-color: black;
}

.laptop:before {
  content: '';
  display: block;
  position: absolute;
  width: 250px;
  height: 30px;
  bottom: -110px;
  left: 50%;
  -webkit-transform: translate(-50%);
  transform: translate(-50%);
  background: black;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  z-index: 1;
}

.laptop .content {
  height: 750px;
  border: none;
  overflow: hidden;
}

.projects .shadow-sm {
  width: 300px;
  text-align: center;
  padding: 0;
  overflow: hidden;
  margin: 15px;
  transition: .3s;
}

.projects .shadow-sm:hover {
  box-shadow: 2px 4px 12px rgba(0,0,0,.2)!important;
}

.projects .shadow-sm div {
  position: relative;
}

.projects .shadow-sm div img, .projects .shadow-sm div video {
  width: 100%;
  height: 280px;
  border: 1px solid #ccc;
  transition: .3s;
}

.projects .shadow-sm:hover > div img, .projects .shadow-sm:hover > div video {
  transform: scale(1.1);
}

.projects .shadow-sm div .overlay {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,.2);
  visibility: hidden;
}

a {
  text-decoration: none !important;
  cursor: pointer;
}

.overlay i {
  color: #ffffff;
  background-color: #000000;
  border-radius: 50%;
  font-size: 2rem;
}

.projects .shadow-sm:hover > div .overlay {
  visibility: visible;
  transform: scale(1.1);
  transition: .3s;
}

.projects .shadow-sm h2, .projects .shadow-sm p {
  text-align: Justify;
  padding: 0 10px;
}

/* Contact section CSS */
#contact .contact-info {
  font-family: 'Source Code Pro', monospace;
  font-size: 14px;
}

.contact-info a {
  color: #1D78D7;
}

.contact-info a:hover {
  text-decoration: underline !important;
  transition: .3s ease;
}

#footer {
  background-color: #1D78D7;
  color: #ffffff;
  font-size: 2rem;
  text-align: center;
  font-family: 'Oswald', sans-serif;
}

@media screen and (max-width: 768px) {
  .smartphone {
    margin: auto;
  }
  .laptop {
    display: none;
  }
  .shadow-sm {
    margin: 0;
    margin: 15px 0;
  }
}

@media screen and (min-width: 768px) {
  .smartphone {
    display: none;
  }
  .laptop {
    display: block;
  }
}